.icon {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 30px;
  height: 30px;
  background-color: #1C1B1B;
  margin: 3px;
  padding: 5px;
  transition: all 0.4s;
}

.icon:hover {
   outline: 1px solid #808080;
 }
.icon a{
  text-decoration: none;
  color: #808080;
}

